<template>
  <div>
    <div class="result-background">
        <div class="result-space-box">
            <h1 class="result-head">企业工业4.0程度</h1>
            <h1 class="result-sub-head">现况分析结果</h1>
            <img class="result-line" src="../../assets/green-line.png" alt="green-line">
            <h2 class="result-mid-head">整体来看</h2>
            <AnalyzeResult :analyzeData='allData.main'></AnalyzeResult>
        </div>
        <div class="result-mid-box-web">
          <div class="result-feedback-data result-feedback-data-web">
              <div class="result-feedback-pie-chart-box">
                  <v-chart class="result-feedback-pie-chart" :options="mainPieSet" ref='echarts' auto-resize/>
                  <div class="result-chart-tips">
                    <div class="result-chart-word">低</div>
                    <div class="result-chart-line"></div>
                    <div class="result-chart-word">高</div>
                  </div>
              </div>
          </div>
          <AnalyzeTab :analyzeData='allData.main'></AnalyzeTab>
        </div>
        <img class="result-light" src="../../assets/light.png" alt="light">
        <div class="result-carousel">
            <swiper class="result-carousel-swipe" :options="swiperOption" ref="mySwiper">
                <div class="swiper-button-prev swiper-button-white" slot="button-prev"></div>
                    <swiper-slide class="result-rotate-box"
                      v-for="(dimension, index) in dimensionData"
                      :key="index"
                    >{{dimension.name}}</swiper-slide>
                  <div class="swiper-button-next swiper-button-white" slot="button-next"></div>
            </swiper>
        </div>

        <div v-if="isShowTransition" class="result-feedback-data">
            <AnalyzeResult :analyzeData='currentDimension'></AnalyzeResult>
            <div class="result-mid-box-web">
              <div class="result-feedback-pie-chart-box result-feedback-data-web">
                  <v-chart class="result-feedback-pie-chart" :options="dimensionPieSet" ref='echarts' auto-resize/>
                  <div class="result-chart-tips">
                    <div class="result-chart-word">低</div>
                    <div class="result-chart-line"></div>
                    <div class="result-chart-word">高</div>
                  </div>
              </div>
              <AnalyzeTab :analyzeData='currentDimension'></AnalyzeTab>
            </div>
        </div>

        <h2 class="result-feedback-suggestion-head">建议作法</h2>
        <div class="result-feedback-suggestion">
          <div class="result-solution" v-for="(solution, index) in currentDimension.solution"
            :key="index">
            <div class="solution-head">{{solution.head}}</div>
            <div class="solution-text">{{solution.text}}</div>
          </div>
        </div>

        <BackToTop v-if="backToTopShow"></BackToTop>
    </div>
    <ContactUs :gaConsult="gaConsult"></ContactUs>
    <Footer></Footer>
  </div>
</template>

<script>

import { swiper, swiperSlide } from 'vue-awesome-swiper'
import 'swiper/dist/css/swiper.css'
import Footer from '@/components/layout/Footer'
import BackToTop from '@/components/layout/BackToTop'
import ContactUs from '@/components/contactUs/ContactUs'
import AnalyzeResult from '@/components/analyze/AnalyzeResult'
import AnalyzeTab from '@/components/analyze/AnalyzeTab'
import { getAnalyzeAndOpt } from '@/API/analyze'

export default {
  name: 'Result',
  components: {
    swiper,
    swiperSlide,
    BackToTop,
    ContactUs,
    Footer,
    AnalyzeResult,
    AnalyzeTab
  },
  data () {
    return {
      gaConsult: [
        {
          category: '/reportsch',
          label: 'S-Rconsult_click'
        },
        {
          category: '/reportsch',
          label: 'S-Rsolution_click'
        }
      ],
      gaSwiper: [
        {label: 'Rproduction_click'},
        {label: 'Resset_click'},
        {label: 'Renergy_click'},
        {label: 'Remployee_click'},
        {label: 'Rinsupply_click'},
        {label: 'Rqm_click'},
        {label: 'Rsudemand_click'},
        {label: 'Rtime_click'}
      ],
      allData: {},
      dimensionData: [],
      currentDimension: {},
      currentChart: [],
      isShowTransition: true,
      backToTopShow: false,
      tabSelect: 'same',
      swiperOption: {
        effect: 'coverflow',
        slidesPerView: 'auto',
        spaceBetween: 30,
        centeredSlides: true,
        initialSlide: 3,
        mousewheel: true,
        grabCursor: true,
        mousewheelControl: true,
        onSlideChangeEnd: function () {
          this.onSwipe()
        },
        navigation: {
          nextEl: '.swiper-button-next',
          prevEl: '.swiper-button-prev'
        },
        coverflowEffect: {
          rotate: 25,
          stretch: 0,
          depth: 0,
          modifier: 1,
          slideShadows: true
        }
      },
      mainPieSet: {
        xAxis: [
          {
            type: 'category',
            data: ['1', '2', '3', '4', '5'],
            axisLine: {
              show: false
            },
            axisLabel: {
              textStyle: {
                color: '#BEC6DB',
                fontSize: '12'
              }
            }
          }
        ],
        yAxis: [
          {
            name: '%',
            splitNumber: 2,
            nameTextStyle: {
              color: '#BEC6DB',
              padding: [0, 28, 0, 0]
            },
            type: 'value',
            axisLine: {
              show: false
            },
            axisLabel: {
              textStyle: {
                color: '#BEC6DB',
                fontSize: '12'
              }
            },
            splitLine: {
              show: false
            },
            axisTick: {
              show: false
            }
          }
        ],
        legend: {
          data: ['你的公司', '其他企业'],
          right: 100,
          itemGap: 30,
          textStyle: {
            color: '#BEC6DB'
          }
        },
        series: [
          {
            name: '你的公司',
            type: 'bar',
            stack: '總量',
            color: '#28FFEA',
            barWidth: '30%',
            label: {
              normal: {
                show: true,
                position: 'top',
                color: '#28FFEA',
                fontSize: 14
              }
            },
            data: []
          },
          {
            name: '其他企业',
            type: 'bar',
            stack: '總量',
            color: '#30394B',
            barWidth: '30%',
            label: {
              normal: {
                show: false,
                position: 'bottom',
                color: '#30394B'
              }
            },
            data: []
          }
        ]
      },
      dimensionPieSet: {
        xAxis: [
          {
            type: 'category',
            data: ['1', '2', '3', '4', '5'],
            axisLine: {
              show: false
            },
            axisLabel: {
              textStyle: {
                color: '#BEC6DB',
                fontSize: '12'
              }
            }
          }
        ],
        yAxis: [
          {
            name: '%',
            splitNumber: 2,
            nameTextStyle: {
              color: '#BEC6DB',
              padding: [0, 28, 0, 0]
            },
            type: 'value',
            axisLine: {
              show: false
            },
            axisLabel: {
              textStyle: {
                color: '#BEC6DB',
                fontSize: '12'
              }
            },
            splitLine: {
              show: false
            },
            axisTick: {
              show: false
            }
          }
        ],
        legend: {
          data: ['你的公司', '其他企业'],
          right: 100,
          itemGap: 30,
          textStyle: {
            color: '#BEC6DB',
            fontSize: 14
          }
        },
        series: [
          {
            name: '你的公司',
            type: 'bar',
            stack: '總量',
            color: '#28FFEA',
            barWidth: '30%',
            label: {
              normal: {
                show: true,
                position: 'top',
                color: '#28FFEA'
              }
            },
            data: []
          },
          {
            name: '其他企业',
            type: 'bar',
            stack: '總量',
            color: '#30394B',
            barWidth: '30%',
            label: {
              normal: {
                show: false,
                position: 'bottom',
                color: '#30394B'
              }
            },
            data: []
          }
        ]
      }
    }
  },
  mounted () {
    window.scrollTo(0, 0)
    window.addEventListener('scroll', this.handleScroll)
    getAnalyzeAndOpt({
      'COMPANY_NO': this.companyNum,
      'LANGUAGE': this.language
    })
      .then(response => {
        this.allData = response.data
        for (let i = 0; i < 8; i++) {
          this.dimensionData.push(this.allData[`dimension${i}`])
        }
        this.currentDimension = this.allData[`dimension${this.swiper.activeIndex}`]

        for (let i = 0; i < 5; i++) {
          if (i === this.allData.main.level - 1) {
            this.mainPieSet.series[1].data.push('-')
            this.mainPieSet.series[0].data.push(this.allData.main.chartData[i])
          } else {
            this.mainPieSet.series[1].data.push(this.allData.main.chartData[i])
            this.mainPieSet.series[0].data.push('-')
          }
        }
        for (let i = 0; i < 5; i++) {
          if (i === this.currentDimension.level - 1) {
            this.dimensionPieSet.series[1].data.push('-')
            this.dimensionPieSet.series[0].data.push(this.currentDimension.chartData[i])
          } else {
            this.dimensionPieSet.series[1].data.push(this.currentDimension.chartData[i])
            this.dimensionPieSet.series[0].data.push('-')
          }
        }
      })
      .catch(err => {
        console.log(err)
      })
    window.addEventListener('resize', () => {
      this.$refs.swiperOption.resize()
    }, false)
    this.swiper.on('slideChange', () => {
      this.onSwipe(this)
      this.currentDimension = this.allData[`dimension${this.swiper.activeIndex}`]
      this.dimensionPieSet.series[0].data = []
      this.dimensionPieSet.series[1].data = []
      for (let i = 0; i < 5; i++) {
        if (i === this.currentDimension.level - 1) {
          this.dimensionPieSet.series[1].data.push('-')
          this.dimensionPieSet.series[0].data.push(this.currentDimension.chartData[i])
        } else {
          this.dimensionPieSet.series[1].data.push(this.currentDimension.chartData[i])
          this.dimensionPieSet.series[0].data.push('-')
        }
      }
    })
  },
  destroyed () {
    window.removeEventListener('scroll', this.handleScroll)
  },
  methods: {
    handleScroll () {
      let scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop
      let offsetTop = document.querySelector('.result-chart-line').offsetTop
      if (scrollTop > offsetTop) {
        this.backToTopShow = true
      } else {
        this.backToTopShow = false
      }
    },
    onSwipe (varuable) {
      this.$ga.event({
        eventCategory: '/reportsch',
        eventAction: 'Click',
        eventLabel: this.gaSwiper[varuable.swiper.activeIndex].label
      })
      // console.log(varuable.swiper.activeIndex)
    }
  },
  computed: {
    swiper () {
      return this.$refs.mySwiper.swiper
    },
    companyNum () {
      return this.$store.state.companyNum
    },
    language () {
      return this.$store.state.language
    }
  }
}
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
    .result-background{
        width: 90%;
        margin: auto;

        .result-space-box{
          position: relative;
          background-image: url('../../assets/space-circle.png');
          background-size: contain;
          background-position-y: center;
          background-repeat: no-repeat;
          background-position-x: center;

          &:before {
            position: absolute;
            content: "";
            display: block;
            width: 100%;
            height: 200px;
            background-image: url('../../assets/space-circle.png');
            background-repeat: no-repeat;
            background-size: cover;
            background-position-x: center;
            background-position-y: center;
            z-index: -1;
            background-color: #000;
            opacity: 0.3;
          }
        }

        .result-head{
            text-align: center;
            color: $theme-color-primary;
            margin-top: 50px;
        }

        .result-sub-head{
          text-align: center;
        }

        .result-mid-head{
            font-size: $theme-font-size-xxx-large;
            font-weight: 300;
            text-align: center;
            letter-spacing: 5px;
        }

        .result-light{
          width: 50px;
          margin-top: -36px;
        }

        .result-carousel{
            display: flex;
            align-items: center;

            .result-carousel-swipe{
                padding: 50px 0px;
            }

            .result-rotate-box{
                width: 150px;
                height: 40px;
                line-height: 40px;
                font-size: 20px;
                background: $theme-backbround-color-gray;
                color: $theme-color-white;
                text-align: center;
                cursor: pointer;
            }

            .swiper-slide-active{
                background: $theme-color-primary;
                color: $theme-color-black;
            }

            .swiper-button-next{

            }
            .swiper-button-prev{

            }
        }

        .result-line{
            max-width: 320px;
            display:block;
            margin:auto;
        }

    }

    .result-feedback-data{

        .result-feedback-text-box{
            display: flex;
            justify-content: space-between;
            align-items: center;
            margin-bottom: 25px;
        }

        .result-feedback-text{
            width: 60%;
        }

        .result-feedback-number{
            height: 66px;
            width: 66px;
            background: url('../.././assets/number-box.png');
            background-repeat: no-repeat;
            background-size: contain;
            background-position-x: center;
            background-position-y: center;
            font-size: $theme-font-size-great;
            text-align: center;
            color: $theme-color-primary;
        }

        .result-feedback-list{
            width: 100%;
            height: 120px;
            display: flex;
            flex-direction: column;
            justify-content: space-around;
            background: rgba(255, 255, 255, 0.24);
            padding: 10px;
            margin: 0px auto 25px;
        }

        .result-feedback-pie-chart-box{
          margin-bottom: 30px;
          border: 1px solid rgba(255, 255, 255, 0.48);
          background: $theme-chart-background-gray;

            .result-feedback-pie-chart{
              width: 100%;
              height: 250px;
              padding-top: 20px;
              transition: all 1.6s;
            }

            .result-chart-tips{
              width: 90%;
              display: flex;
              padding: 0px 0px 10px 10%;
            }

            .result-chart-word{
              width: 10%;
              height: 30px;
              text-align: center;
              line-height: 30px;
            }

            .result-chart-line{
              width: 75%;
              height: 30px;
              background-image: url('../../assets/level_outlined.svg');
              background-repeat: no-repeat;
              background-size: contain;
              background-position-x: center;
              background-position-y: center;
            }
        }

        .result-feedback-theme-color{
          color: $theme-color-primary;
        }
        .result-feedback-medium-size{
          font-size: $theme-font-size-large;
        }
    }

    .result-feedback-suggestion{
      margin-bottom: 40px;

      .result-feedback-suggestion-head{
        font-size: $theme-font-size-xxx-large;
        text-align: center;
        margin: 30px 0px;
        font-weight: 300;
      }
    }

    .result-solution{
      margin-bottom: 20px;

      .solution-head{
        font-size: $theme-font-size-large;
        width: 100%;
        padding: 10px;
        background: $theme-color-thin-black;
      }

      .solution-text{
        width: 100%;
        padding: 10px;
        background: $theme-backbround-color-gray;
      }
    }

  @media( min-width: 767px ){
    .result-mid-box-web{
      width: 717px;
      height: 270px;
      display: flex;
      margin: auto;
    }

    .result-feedback-data-web{
      width: 50%;
      margin-right: 20px;
    }

    .analyze-tab-box{
      width: 50%;
    }

    .result-feedback-data .result-feedback-pie-chart-box .result-feedback-pie-chart{
      height: 207px;
    }

    .result-feedback-data .result-feedback-pie-chart-box{
      margin-bottom: 20px;
    }

    .result-carousel{
      width: 717px;
      margin: auto;
    }

    .result-feedback-suggestion-head{
      width: 717px;
      margin: 30px auto;
    }

    .result-feedback-suggestion{
      display: flex;
      width: 717px;
      margin: 0px auto 40px;
      flex-wrap: wrap;
      justify-content: space-between;

      .result-solution{
        width: 48%;
      }
    }
  }

  @media( min-width: 1023px ){
    .result-mid-box-web{
      width: 960px;
    }

    .result-carousel{
      width: 960px;
    }

    .result-feedback-suggestion-head{
      width: 960px;
    }

    .result-feedback-suggestion{
      width: 960px;
    }
  }

</style>



